
header {
  position: sticky;
  height: 100px;
  top: 0;
  left: 0;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 20px;
  z-index: 2;
  background: #24292e;
  box-shadow: 0 5px 5px -2px #333;
}
header li {
  display: inline-block;
  color: #ccc;
}
header button {
  cursor: pointer;
  background: none;
  border: 0;
  font-size: 1.3rem;
  color: #ccc;
}
header button.active {
  font-size: 2rem;
  font-weight: bold;
  color: #eee;
}
header button.active:hover {
  text-decoration: none;
  cursor: default;
}
header button:hover {
  text-decoration: underline;
  color: #eee;
}
header button:hover {
  text-decoration: underline;
  cursor: pointer;
}
main {
  padding: 0 20px;
  position: relative;
  padding-bottom: 82px;
}
main aside {
  width: 15%;
  min-width: 200px;
  position: sticky;
  top: 100px;
  left: 0;
  height: calc(100vh - 100px - 82px);
  float: left;
  overflow-y: scroll;
  padding-top: 20px;
  box-shadow: -5px 0 5px -2px #ccc inset;
}
main > div {
  width: 85%;
  padding-left: 20px;
  padding-top: 20px;
  margin-left: 15%;
}
header h1 {
  display: block;
  width: 359px;
  height: 90px;
  color: transparent;
}
header h1.ultra-sun-moon {
  background: url(26d3d92f0e15bd8289eb531f244c76ed.png);
  background-size: cover;
}
header h1.sun-moon {
  background: url(d68fedc80d1f46ee61e71131b45806ef.png);
  background-size: cover;
}
.back-to-top {
  width: 50px;
  height: 50px;
  background: rgba(102, 102, 102, 0.6);
  color: #fff;
  position: fixed;
  bottom: 132px;
  right: 0;
  display: block;
  text-align: center;
}
.back-to-top svg {
  width: 35px;
  margin-top: 5px;
  height: 35px;
  fill: #fff;
}
.game-ultra-sun-moon,
.game-sun-moon {
  display: none;
}
.sun-moon .game-sun-moon,
.ultra-sun-moon .game-ultra-sun-moon {
  display: block;
}
